import PropTypes from "prop-types"
import React, { useEffect, useState, useLayoutEffect } from "react"
import { Link } from "@reach/router"

import "./menu.scss"
import LanguageSwitcher from "../LanguageSwitcher"
import { GiHamburgerMenu } from "react-icons/gi"

import { getWindowSize } from "../../utils/window"

const Menu = ({ location, lang, menuItems, languages, mobile }) => {

  const [menuVisibility, setMenuVisibility] = useState(false)

  const renderMenu = () => {
    if (mobile === true) {
      return (
        <nav className="main-menu--mobile">
          <div onClick={() => setMenuVisibility(!menuVisibility)}
               className="main-menu__icon-wrapper">
            <GiHamburgerMenu className="main-menu__icon"/>
          </div>
          <ul
            className={`main-menu__items${menuVisibility ? " main-menu__items--active" : ""}`}>
            {menuItems.map(menuItem => (
              <li key={menuItem.name} className="main-menu__item">
                <Link to={menuItem.path}>{menuItem.name}</Link>
              </li>
            ))}
          </ul>
        </nav>
      )
    }
    else if (mobile === false) {
      return (
        <nav className="main-menu">
          <ul className="main-menu__items">
            {menuItems.map(menuItem => (
              <li key={menuItem.name} className="main-menu__item">
                <Link to={menuItem.path}>{menuItem.name}</Link>
              </li>
            ))}
          </ul>
          <LanguageSwitcher
            location={location} lang={lang}
            languages={languages}/>
        </nav>
      )
    }
    else if (typeof mobile === "undefined")
      console.log('undefined')
  }

  return (
    <>
      {console.log(mobile)}
      {renderMenu()}
    </>
  )
}

Menu.propTypes = {
  menuItems: PropTypes.arrayOf(PropTypes.object),
  languages: PropTypes.arrayOf(PropTypes.string)
}

Menu.defaultProps = {
  menuItems: [],
  languages: []
}

export default Menu
