import { Link } from "gatsby"
import PropTypes from "prop-types"
import React, { useEffect, useState } from "react"

import Menu from "../menu"

import "./header.scss"
import data from "../../data/main-page.en.json"
import { getWindowSize } from "../../utils/window"

const Header = ({ location, lang }) => {

  const [isMobile, setIsMobile] = useState((getWindowSize().width <= 1024))

  useEffect(() => {
    console.log("isMobile: " + isMobile)
    window.addEventListener("resize", e => {
      if (e.target.innerWidth > 1024) {
        setIsMobile(false)
      }
      else setIsMobile(true)
    })
  })

  return (
    <header className="main-header">
      <div className="main-header__content">
        <h1 className="main-header__logo">
          <Link to={lang === "en" ? "/" : "/pl"}>
            <span className="main-header__logo--accent">Kamil</span>Nawrot
          </Link>
        </h1>
        <Menu
          location={location}
          lang={lang}
          menuItems={data.header.menu}
          languages={data.header.languages}
          mobile={isMobile}
        />
      </div>
    </header>
  )
}

Header.propTypes = {
  location: PropTypes.object,
  lang: PropTypes.string.isRequired,
  menuItems: PropTypes.arrayOf(PropTypes.object),
  languages: PropTypes.arrayOf(PropTypes.string)
}

Header.defaultProps = {
  lang: "en",
  menuItems: [],
  languages: []
}

export default Header
